import http from "frontend/src/axios";

const endpointMaster = '/rubix-master';

export const fetchOpenVPNConfig = (globalUuid, apiService, serviceName) => {
  return http.get(`${endpointMaster}/openvpn-config/${globalUuid}`).then(({data})=> {
    return apiService['saveOpenVPNConfig'](serviceName, data, globalUuid).then(({data}) => data)
  })
}
