<template>
  <v-card :loading="isFetchingConfig">
    <v-card-title>Edit Config</v-card-title>
    <v-card-text>
      <v-container>
        <v-jsoneditor
          v-if="configType === jsonConfigType"
          :options="{ mode: 'code' }"
          v-model="config"
          :plus="false"
          height="400px"
        ></v-jsoneditor>
        <v-textarea
          v-if="configType === envConfigType"
          outlined
          name="input-7-4"
          label="Enter ENV config"
          v-model="config"
        ></v-textarea>
        <div v-if="configType === openVpnConfigType">
          <v-row dense>
            <v-col cols="12" class="mb-5">
              <v-file-input
                :rules="[rules.ovpnExtensionValidator]"
                counter
                show-size
                v-model="file"
                @change="onFileSelect"
                truncate-length="15"
              ></v-file-input>
            </v-col>
            <v-col cols="12">
              <v-textarea outlined name="input-7-4" label="OpenVPN Config" v-model="config"></v-textarea>
            </v-col>
          </v-row>
        </div>
        <div v-if="configType === ymlConfigType && !isFetchingConfig">
          <yaml-editor :code="config" @input="onValueChange"></yaml-editor>
        </div>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-btn small text @click="closeDialog">Cancel</v-btn>
      <v-btn
        v-if="configType === openVpnConfigType && this.$store.state.mqttRestBridgeConfig.enabled"
        x-small
        class="mr-1 btn-height-22 white--text"
        color="warning"
        @click="resetConfig"
        :loading="isRestartConfig"
        >Restart Config
      </v-btn>
      <v-btn
        x-small
        class="mr-1 btn-height-22 white--text"
        color="primary"
        @click="saveConfig"
        :loading="isSavingConfig"
        >Save
      </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as appsService from '../../services/appsService';
import * as slaveAppsService from '../../services/slaveAppsService';
import * as openVPNService from '../../services/openVPNService';
import YamlEditor from '../Common/YamlEditor.vue';
import {
  APP_CONFIG_TYPES,
  JSON_CONFIG_TYPE,
  ENV_CONFIG_TYPE,
  OPENVPN_CONFIG_TYPE,
  YML_CONFIG_TYPE,
  SERVICE_NAMES,
} from '../../constants/appConstant';
import { getAppSlaveDeviceInfo } from 'frontend/src/services/slaveAppsUiService';
export default {
  name: 'app-edit-config',
  props: ['appDetail', 'closeDialog', 'isSlaveApp'],
  components: { YamlEditor },
  data() {
    return {
      config: null,
      slaveId: null,
      apiOpenVPNService: null,
      apiService: null,
      isFetchingConfig: false,
      isSavingConfig: false,
      isRestartConfig: false,
      appDetails: [],
      isArray: false,
      appType: null,
      configType: null,
      jsonConfigType: JSON_CONFIG_TYPE,
      envConfigType: ENV_CONFIG_TYPE,
      openVpnConfigType: OPENVPN_CONFIG_TYPE,
      ymlConfigType: YML_CONFIG_TYPE,
      file: null,
      rules: {
        ovpnExtensionValidator: value =>
          (value && value.name.toLowerCase().includes('.ovpn')) || 'Extension not supported.',
      },
    };
  },
  mounted() {
    if (this.isSlaveApp) {
      this.slaveId = this.appDetail.slaveId;
      this.apiService = slaveAppsService;
    } else {
      this.apiService = appsService;
    }
    this.apiOpenVPNService = openVPNService;
    this.appDetails = Array.isArray(this.appDetail) ? this.appDetail : [this.appDetail];
    this.appType = this.appDetails[0].app_type;
    this.fetchAppConfig();
    this.configType = this.getConfigType(this.appType, this.appDetails[0].service);
  },
  methods: {
    getConfigType(appType, serviceName) {
      if (serviceName === SERVICE_NAMES.RUBIX_FLOW_FRAMEWORK || serviceName === SERVICE_NAMES.RUBIX_BROKER) {
        return YML_CONFIG_TYPE;
      } else if (appType) {
        return APP_CONFIG_TYPES[appType] ? APP_CONFIG_TYPES[appType] : YML_CONFIG_TYPE;
      }
      return YML_CONFIG_TYPE;
    },
    getAppDetailType(appDetail) {
      const isArray = Array.isArray(appDetail) && appDetail.length > 0;
      const firstItem = isArray && appDetail[0];
      return { isArray, firstItem };
    },
    onFileSelect() {
      if (!this.file) {
        return;
      }
      this.file
        .text()
        .then(val => {
          this.config = val;
        })
        .catch(() => {
          this.$toasted.show('Cannot parse file', { type: 'error' });
        });
    },
    fetchAppConfig() {
      const serviceName = this.appDetails[0].service;
      const appType = this.appType;
      const slaveId = this.appDetails[0].slaveId;
      this.isFetchingConfig = true;
      this.apiService['fetchAppConfig'](serviceName, appType, { restrictToast: true, slaveId: slaveId })
        .then(data => {
          this.config = data;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingConfig = false;
        });
    },
    resetConfig() {
      this.isRestartConfig = true;
      const serviceDisplayName = this.appDetails[0].display_name;
      const selectedDeviceInfo = getAppSlaveDeviceInfo();
      Promise.all(
        this.appDetails.map(appDetail =>
          this.apiOpenVPNService['fetchOpenVPNConfig'](
            (selectedDeviceInfo && selectedDeviceInfo.global_uuid) ||
              appDetail.slaveId ||
              this.$store.state.setting.global_uuid,
            this.apiService,
            appDetail.service,
          ),
        ),
      )
        .then(data => {
          this.$toasted.show(`Successfully updated config for ${serviceDisplayName}`);
          this.closeDialog();
        })
        .catch(err => {
          this.$toasted.show(`Failed to update config for ${serviceDisplayName}`, { type: 'error' });
        })
        .finally(() => {
          this.isRestartConfig = false;
        });
    },
    saveConfig() {
      this.isSavingConfig = true;
      const serviceDisplayName = this.appDetails[0].display_name;

      Promise.all(
        this.appDetails.map(appDetail =>
          this.apiService['saveAppConfig'](appDetail.service, this.appType, this.config, appDetail.slaveId),
        ),
      )
        .then(data => {
          this.$toasted.show(`Successfully updated config for ${serviceDisplayName}`);
          this.closeDialog();
        })
        .catch(err => {
          this.$toasted.show(`Failed to update config for ${serviceDisplayName}`, { type: 'error' });
        })
        .finally(() => {
          this.isSavingConfig = false;
        });
    },
    onValueChange(value) {
      this.config = value;
    },
  },
};
</script>
